import Cookies from 'universal-cookie';

class Utils {
    static BASE_URL = "https://servicy.herokuapp.com/api"
    static cookies = new Cookies();

    static getFormatStatus(status) {
        switch (status) {
            case 'active':
                return 'Kch hot'
            case 'inactive':
                return 'Cha kch hot'
            case 'pending':
                return 'Ch duyt'
            case 'running':
                return 'ang chy'
            case 'done':
                return 'Ht hn'
            default:
                return status
        }
    }

    static getFormatAdtype(adType) {
        switch (adType) {
            case 'Enterprise':
                return 'Doanh nghip'
            case 'Normal':
                return 'Thng'
            case 'Advance':
                return 'Cao cp'
            case 'Trial':
                return 'Th nghim'
            case 'Normal':
                return 'Trung bnh'
            default:
                return adType
        }
    }

    static getFormatRole(role) {
        switch (role) {
            case 'admin':
                return 'Qun tr vin'
            case 'provider':
                return 'Nh cung cp'
            case 'user':
                return 'Ngi dng thng'
            default:
                return role
        }
    }

    static getFormatDate(mongoDate) {
        let jsDate = new Date(mongoDate)
        return `${jsDate.getFullYear()}-${jsDate.getMonth() + 1}-${jsDate.getDate()} ${jsDate.getHours()}:${jsDate.getMinutes()}:${jsDate.getSeconds()}`
    }
}

export default Utils;